<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <h2><span>文件路径 : </span>{{file_path}}</h2>
    <div>
      <uploader :options="options_image" class="uploader-example" :file-status-text="statusText" ref="uploader"
        @file-success="fileSuccess">
        <uploader-unsupport></uploader-unsupport>
        <uploader-drop>
          <uploader-btn :attrs="attrs">上传图片</uploader-btn>
        </uploader-drop>
        <uploader-list></uploader-list>
      </uploader>
    </div>

    <div>
      <uploader :options="options_file" class="uploader-example" :file-status-text="statusText" ref="uploader"
        @file-success="fileSuccess">
        <uploader-unsupport></uploader-unsupport>
        <uploader-drop>
          <uploader-btn>上传文件</uploader-btn>
        </uploader-drop>
        <uploader-list></uploader-list>
      </uploader>
    </div>

    <div>
      <uploader :options="options_avatar" class="uploader-example" :file-status-text="statusText" ref="uploader"
        @file-success="fileSuccess">
        <uploader-unsupport></uploader-unsupport>
        <uploader-drop>
          <uploader-btn :attrs="attrs">上传头像</uploader-btn>
        </uploader-drop>
        <uploader-list></uploader-list>
      </uploader>
    </div>
  </div>
</template>

<script>
  import showdown from 'showdown';
  export default {
    name: 'HelloWorld',
    data() {
      return {
        text: '',
        file_path: '',
        msg: 'Welcome to My Website !',
        uploader_key: new Date().getTime(), //这个用来刷新组件--解决不刷新页面连续上传的缓存上传数据（注：每次上传时，强制这个值进行更改---根据自己的实际情况重新赋值）
        options_image: {
          target: 'http://127.0.0.1:9999/upload/image',
          testChunks: false,
          fileParameterName: 'image',
        },
        options_file: {
          target: 'http://127.0.0.1:9999/upload/file',
          testChunks: false,
          fileParameterName: 'file',
        },
        options_avatar: {
          target: 'http://127.0.0.1:9999/upload/avatar',
          testChunks: false,
          fileParameterName: 'avatar',
        },
        statusText: {
          success: '成功了',
          error: '出错了',
          uploading: '上传中',
          paused: '暂停中',
          waiting: '等待中'
        },
        attrs: {
          accept: 'image/*'
        }
      }
    },
    methods: {
      //上传成功的事件
      fileSuccess(rootFile, file, message, chunk) {
        this.file_path = message;
      },
    },
    mounted() {
      // 获取uploader对象
      this.$nextTick(() => {
        window.uploader = this.$refs.uploader.uploader
      })
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .uploader-example {
    width: 880px;
    padding: 20px;
    margin: 40px auto 0;
    font-size: 12px;
    box-shadow: 0 0 10px rgba(0, 0, 0, .4);
  }

  .uploader-example .uploader-btn {
    margin-right: 4px;
  }

  .uploader-example .uploader-list {
    max-height: 440px;
    overflow: auto;
    overflow-x: hidden;
    overflow-y: auto;
  }

</style>



